.main {
    width: 60%;
    border: 1px solid #ccc;
    margin: auto;
    margin-right: 10%;
    position: relative;
    margin-bottom: 50px;

}

.additional {
    width: 40%;
    border: 1px solid #ccc;
    margin: auto;

}

.frame1 {
    width: 48%;
    display: flex;
    border: 1px solid #ccc;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 10px;
}

.frame2 {
    width: 26%;
    border: 1px solid #ccc;
    display: flex;
    justify-content: space-around;
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 10px;
}

.frame3 {
    width: 40%;
    border: 1px solid #ccc;
}

.frame4 {
    width: 70%;
    display: flex;
    flex-direction: column;
}

.frame5 {
    width: 26%;
    border: 1px solid #ccc;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 10px;
    text-align: end;
    padding-left: 10px;
    margin: auto;
    display: block !important;
}

.inner1 {
    display: flex;
    flex-direction: row;
}

.inner2 {
    display: flex;
    flex-direction: row;
}

.column {
    display: flex;
    flex-direction: column;
}


.altwrapper {
    border: 1px solid #ccc;
    display: flex;
    background-color: white;
    height: 700px;
}

.streetmargin {
    margin-left: 3px;
}

.mainwrapper {
    border: 1px solid #ccc;
    display: flex;
}

.resimg {
    height: 80px;
    width: 100px;
    object-fit: fill;
}

.mainalt {
    width: 100%;
    position: relative;
    border: 1px solid #ccc;
    border-top: none;
    border-bottom: none;
}



.altadress {
    display: flex;
    font-family: system-ui;
    color: #363636;
    font-weight: 500;
}

.altkind {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    font-family: system-ui;
    color: #363636;
    font-weight: 500;
    font-size: 12px;
}

.altimgwrapper {
    width: 35%;
    display: flex;
}

.altimg {
    height: 120px;
    width: 200px;
    padding: 5px;
    margin: auto;
    border-radius: 8%;
    margin-left: 10px;
}

.innernumberssmall {
    color: #363636;
    font-weight: 500;
    font-size: 12px;
}

.altM2 {
    display: flex;
    justify-content: space-around;
    text-align: center;
    font-family: system-ui;
    color: #363636;
    font-weight: 500;
}

.altM2inner {
    display: flex;
    flex-direction: column;
}

.orangevertical {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 35%;
}

.greenbutton {
    padding: 0;
    width: 100%;
    background-color: #0fca80;
    color: #fff;
    font-size: 14px;
    font-weight: 500;
    border-radius: 2px;
    border: 0px;
    padding: 10px;
    height: 40px;
    margin-left: 10px;
    width: 180px;
}

.lastorangepart {
    display: flex;
    flex-direction: column;
    width: 35%;
    text-align: center;
    justify-content: space-around;
}

.mainwhite {
    background-color: #fff;
    width: 65%;
    height: 450px;
    display: flex;
    flex-direction: column;
}

.phimg {
    height: 42%;
    width: 70%;
    margin: auto;
    margin-right: 4px;
}

.sideadd {
    background-color: #f0f0f0;
    width: 35%;
    height: 450px;
    display: flex;
    flex-direction: column;
}

.wrapperwhite {
    display: flex;
    flex-direction: row-reverse;
}

.propscol {
    display: flex;
}

.mainpropscol {
    display: flex;
    flex-direction: column;
    padding-right: 10px;
    padding-left: 10px;
}

.title {
    padding: 10px;
    color: #363636;
    font-family: system-ui;
    font-size: 14px;
    font-weight: 500;
}

.text {
    padding: 10px;
    color: #363636;
    font-family: system-ui;
    font-size: 14px;
}

.propstextb {
    font-family: system-ui;
    font-size: 12px;
    color: #363636;
    font-weight: 500;
}

.propstext {
    font-family: system-ui;
    font-size: 12px;
    color: #363636;
    margin-left: 5px;
}

.methaprops {
    display: flex;
    justify-content: flex-start;
}

.iconscol {
    display: flex;
    flex-direction: column;
    padding-top: 30px;
    height: 100px;
    justify-content: space-around;
}

.mainicon {
    display: flex;
    justify-content: space-around;
}

.activecolor {
    color: #363636;
    display: flex;
}

.deactivatedcolor {
    color: #ccc;
    display: flex;
}

.icontext {
    font-size: 12px;
    margin-right: 4px;

}

.firstadd {
    width: 200px;
    margin: auto;
    margin-top: 5px;
    margin-bottom: 5px;
}

.secondadd {
    border-radius: 4px;
    width: 180px;
    margin: auto;
    margin-top: 5px;
    margin-bottom: 5px;
}

.updatetitle {
    font-family: system-ui;
    color: #9999;
    font-size: 12px;
}

.flexphone {
    display: flex;
    cursor: pointer;
}

.send {
    display: flex;
    cursor: pointer;
    justify-content: space-between;
    color: #ff7100;
    font-family: system-ui;
    font-size: 13px;
    font-weight: 600;
}

.phoneicon {
    position: absolute;
    padding: 10px;
    color: white;
    margin-right: 3px;
}

.phonedetails {
    position: absolute;
    width: 100%;
    max-width: 180px;
    background: #fff;
    color: #000;
    box-shadow: 0 2px 4px 0 RGB(0 0 0 / 30%);
    height: 150px;
    top: 120px;
}

.bar {
    height: 1px;
    width: 100%;
    background: #ccc;
}

.bar2 {
    height: 1px;
    width: 100%;
    background: #ccc;
}

.phonedetailspadding {
    padding-top: 15px;
    padding-bottom: 15px;
    font-family: system-ui;
    color: #363636;
    font-weight: 14px;
    font-size: 14px;
    font-weight: 500;
}

.regularcolor {
    position: relative;
    top: 15px;
    font-family: system-ui;
    font-size: 13px;
    color: #363636;
}

.orangecolor {
    position: relative;
    top: 15px;
    font-family: system-ui;
    font-size: 13px;
    color: #f77100;
}

.modal {
    position: fixed;
    z-index: 1;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
}

.innermodal {
    position: relative;
    width: 260px;
    height: 150px;
    background: white;
    box-shadow: 0 2px 6px 0 RGB(0 0 0/10%);

    margin: auto;
    border-radius: 6px;
    padding: 20px;
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}

.innermodal2 {
    position: relative;
    width: 350px;
    height: 460px;
    background: white;
    box-shadow: 0 2px 6px 0 RGB(0 0 0/10%);
    margin: auto;
    border-radius: 6px;
    padding: 20px;
    margin-top: -10px;
    display: flex;
    flex-direction: column;

}

.mainsendtitle {
    font-family: system-ui;
    color: #363636;
    font-weight: 500;
}

.sendtext {
    color: #00000080;
    font-family: system-ui;
    font-size: 13px;
    font-weight: 500;
}

.mailflex {
    display: flex;
    justify-content: space-between;
    color: #363636;
    font-family: system-ui;
    font-weight: 600;
}

.mailinput {
    height: 42px;
    width: 80%px;
    width: 100%;
    cursor: pointer;
    height: 40px;

    background-color: #fff;
    font-size: 14px;
    text-indent: 8px;
    font-family: Rubik, sans-serif;
    margin-top: 5px;
    max-height: 500px;
    border-top: none;
    border-right: none;
    border-left: none;
}

.textarea {
    margin-top: 30px;
    resize: none;
    border-color: #686868;
    height: 100px;
    padding: 3px;

}

.cursor {
    cursor: pointer;
}

.sendmailorangebutton {
    color: white;
    background-color: #f77100;
    padding: 5px;
    border: none;
    width: 60%;
    padding: 10px;
    border-radius: 4px;
    font-family: system-ui;
    font-weight: 500;
}

.buttonswrapper {
    width: 100%;
    padding: 10px;
    display: flex;
    justify-content: space-around;
    cursor: pointer;
}

.cancel {
    position: relative;
    top: 10px;
    color: #363636;
    font-family: system-ui;
    font-weight: 500;
    font-size: 14px;
    cursor: pointer;
}