.wrapper {
    width: 100%;
    display: flex;
    background-color: #f9f9f9;
    color: #363636;
}

.wrapper1 {
    width: 80%;

    margin: auto;
}

.flexrow {
    display: flex;
    width: 100%;
    flex-direction: column;
}

.whitebox {
    width: 100%;
    background: white;
    height: 120px;
    border: 1px solid #dbdbdb;
    border-radius: 6px;

}

.whitebox:hover {
    cursor: pointer;
    background-color: rgb(233, 229, 229);

}

.title {
    padding: 10px;
    font-weight: 500;
    font-family: system-ui;

}

.flex {
    display: flex;
    margin-right: 10px;
    width: 70%;
}

.bar {
    height: 16px;
    width: 1.5px;
    background-color: #ccc;
    margin-right: 8px;
    margin-left: 4px;
    position: relative;
    top: 2px;
}

.nothing {
    display: none;
}

.variable {
    color: #999;
    font-size: .875rem;
}

.values {
    color: #363636;
    font-size: .875rem;
    margin-right: 8px;
}

.topline {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    margin: auto;
    color: #363636;
}

.xmargin {
    margin-left: 15px;
}

.time {
    position: relative;
    float: left;
    margin-top: 15px;
    margin-left: 15px;
    font-size: 14px;
    color: #6c6c6c;
}

.titledate {
    padding: 10px;
    font-weight: 500;
    font-family: system-ui;
}

.largetitle {
    padding: 10px;
    font-weight: 500;
    font-family: system-ui;
    font-size: 19px;
}

.greytitle {
    color: #c1baba;
    font-size: 13.5px;
    text-decoration: underline;
    font-weight: 500;
    cursor: pointer;
}

.toptitle {
    display: flex;
    align-items: center;
    justify-content: space-between;
}