.btn {
    box-sizing: border-box;
    height: 31px;
    line-height: 28px;
    width: 140px;
    border: 1px solid #ccc;
    border-radius: 20px;
    background-color: #fff;
    text-align: center;
    display: block;
    cursor: pointer;
    color: #363636;
    text-decoration: none;
    direction: rtl;
    display: flex;
    justify-content: space-between;
    float: left;
    margin-top: 20px;
    margin-left: 20px;
}

.text {
    color: #363636;
    font-size: 14px;
    margin-right: 10px;
    font-family: system-ui;
}

.img {
    height: 20px;
    position: relative;
    top: 6px;
    left: 7px;
}

.img2 {
    position: relative;
    top: 100px;
    right: 400px;
}