.wrapper {
    position: absolute;
    top: 65%;
    width: 200px;
    height: 200px;

    z-index: 1;
}

.inside {
    background-color: #fff;
    box-shadow: 0 2px 6px 0 RGB(0 0 0 / 10%);
    box-sizing: border-box;
    min-width: 180px;
    min-height: 80px;
    position: relative;
    top: 35px;
}

.bars {
    background-color: #f9f9f9;
    height: 40px;
    width: 100%;
    margin-bottom: 20px;
    box-shadow: 0 2px 6px 0 RGB(0 0 0 / 10%);
}

.flex {
    display: flex;
}

.flex2 {
    display: flex;
    margin-right: 10px;
}

.boxwidth {
    width: 20px;
    margin-right: 20px;

}

.allestates {
    margin: auto;
    margin-right: 10px;
}

.select {
    border: 1px solid #f9f9f9;
    background: #f9f9f9;
    color: #363636;
    margin-left: 20px;
    height: 10px;
    margin: auto;
    margin-left: 20px;
}

.line {
    background-color: #ebe9e9;
    width: 90%;
    height: 1px;
    margin: auto;

}

.choise {
    padding-bottom: 15px;
    position: relative;
    right: 135px;
    color: #ff7100;
    font-family: system-ui;
    font-size: 14px;
    cursor: pointer;

}