.upperbar {
    box-shadow: 0 0 2px 0 RGB(0 0 0 / 14%), 0 2px 2px 0 RGB(0 0 0 / 12%), 0 1px 3px 0 RGB(0 0 0 / 20%);
    height: 50px;
    margin-bottom: 4px;
}

.flexcol {
    display: flex;
    flex-direction: column;
}

.headerimg {
    margin: auto;
    width: 80%;
}

.whiteBG {
    box-shadow: 0 2px 6px 0 RGB(0 0 0 / 10%);
    height: 175px;
    width: 78%;
    margin: auto;
    background-color: #fff;
    margin-top: 20px;

}

.whiteBGalt {
    box-shadow: 0 2px 6px 0 RGB(0 0 0 / 10%);
    height: 750px;
    width: 85%;
    margin: auto;
    background-color: #fff;
    margin-top: 20px;
}

.mainBG {
    background-color: #f9f9f9;
}

.header {
    width: 77%;
    margin: auto;
}

.headerwrapper {
    display: flex;
    position: relative;
}


.searchtop {
    display: flex;
    justify-content: space-between;
    padding: 10px;
}

.searchdown {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-end;
    flex-wrap: wrap;
}

.column {
    display: flex;
    flex-direction: column;
    position: relative
}

.fromto {
    display: flex;
    width: 150px;
}

.pluswrapper {

    margin: auto;
    border-radius: 25px;
    height: 10px;
    width: 10px;
    animation-fill-mode: backwards;
    display: flex;
    margin-left: 10px;

}

.advancedbutton {
    height: 44px;
    display: flex;
    direction: ltr;
    text-align: center;
    border-radius: 4px;
    border: 1px solid;
    border-color: #F0F0F0;
    align-items: center;
    width: 110px;
    cursor: pointer;
}

.orangebutton {
    height: 44px;
    display: flex;
    direction: ltr;
    text-align: center;
    background-color: #ff7100;
    border: 1px solid #ff7100;
    border-radius: 4px;
    align-items: center;
    color: white;
    font-size: 12px;
    font-family: system-ui;
    font-weight: 500;
    cursor: pointer;
}

.orangebutton:hover {
    background-color: #ec6d04;

}

.city {
    width: 170px;
}

.price {
    width: 100%;
}

.adresswrapper {
    position: absolute;
    top: 500px;
}

.adress {
    height: 400px;
    width: 450px;
    background-color: #fff;
    box-shadow: 0 2px 6px 0 RGB(0 0 0/10%);
    position: absolute;
    top: 90px;
    padding: 5px;
    z-index: 20;

}

.title1 {
    padding: 5px;
    font-size: 14px;
    color: #363636;
    font-family: system-ui;
    font-weight: 700;
    background: #f4f4f4;
}

.flexrow {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
}

.text {
    font-size: 14px;
    color: #363636;
    font-family: system-ui;
    padding-top: 10px;
    padding-bottom: 10px;

}

.text2 {
    font-size: 14px;
    color: #808080;
    font-family: system-ui;
    padding-top: 10px;
    padding-bottom: 10px;

}

.select {
    width: 150px;
    cursor: pointer;
    height: 40px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
    font-size: 14px;
    text-indent: 8px;
    font-family: Rubik, sans-serif;
    margin-top: 5px;
    max-height: 500px;
}

.rooms {
    width: 90px;
    cursor: pointer;
    height: 40px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
    font-size: 14px;
    text-indent: 8px;
    margin-top: 5px;
    font-family: system-ui;
    color: #818181;
    max-height: 500px;
}

.innerpluswrapper {
    border: 1px solid #363636;
    border-radius: 20px;
    display: flex;
}

.iconplus {
    border: 1px solid #363636;
    border-radius: 20px;
    height: 10px;
}

.title3 {
    font-size: 12px;
    font-family: system-ui;
    color: #363636;
    font-weight: 500;
}

select:required {
    color: red;
}

.greatersign {
    position: absolute;
    transform: rotate(90deg);
    height: 10px;
    top: 363px;
    right: 413px;
}

.greatersignalt {
    position: absolute;
    transform: rotate(270deg);
    height: 10px;
    top: 363px;
    right: 413px;
}

.greatersign2 {
    position: absolute;
    transform: rotate(90deg);
    height: 10px;
    top: 363px;
    color: #363636;
    right: 515px;
}

.greatersignalt2 {
    position: absolute;
    transform: rotate(270deg);
    height: 10px;
    top: 363px;
    color: #363636;
    right: 515px;
}

.fontwrapper1 {
    position: relative;
    top: -340px;
    right: -280px;
}

.fontwrapper2 {
    position: relative;
    top: -340px;
    right: -438px;

}

.iconflex {
    display: flex;
    border: 1px solid #ff7100;
    padding: 10px;
    color: #ff7100;
    font-size: 14px;
    font-weight: 600;
}

.bell {
    margin: auto;
    margin-left: 5px;
}

.titlelarge {
    font-size: 20px;
    color: #363636;
    font-family: system-ui;
    font-weight: 500;
    padding: 5px;
}

.titlelargeorange {
    margin-left: 3px;
    color: #ff7100;
    border-bottom: 2px dotted;
}

.methawrapper {
    display: flex;
    flex-direction: row;
}

.overflow {
    overflow: visible !important;
}

.commercialwrapper {
    display: flex;
    flex-direction: row;
    width: 70%;
    margin: auto;
    margin-top: 20px;
    justify-content: space-between;

}

.commercialtext {
    text-align: center;
    font-family: system-ui;
    font-size: 13px;
    font-weight: 500;
    color: #363636;
    background: white;
    margin-top: 3px;

}

.marginleft {
    margin-left: 5px;
    cursor: pointer;
}

.comercialimg {
    width: 85px;
}