.mainwrapper {
    position: absolute;
    right: 35%;
    top: 105%;
    z-index: 25;
}

.wrapper {
    background-color: #fff;
    box-shadow: 0 2px 6px 0 RGB(0 0 0/10%);
    box-sizing: border-box;
    min-width: 180px;
    min-height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.unit {
    width: 75px;
    cursor: pointer;
    height: 40px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
    font-size: 14px;
    text-indent: 8px;
    font-family: Rubik, sans-serif;
    margin-top: 5px;
    max-height: 500px;

}