.column {
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-bottom: 0;
    margin-top: 0;
}

.flex {
    display: flex;
}

.flex2 {
    display: flex;
    justify-content: space-between;
}


.greybutton {
    display: flex;
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 20px;
    width: 100px;
    align-items: center;
    justify-content: center;

}

.flexcolumn {
    display: flex;
    flex-direction: column;
}

.mainBG {
    background-color: #fff3f3;
}

.whiteBG {
    display: flex;
    flex-direction: row;
    padding-top: 30px;
    background: white;
    margin-bottom: 30px;
    height: 600px;
    padding: 50px;
    box-shadow: 0 5px 6px 0 RGB(0 0 0 / 10%);
    margin-right: 50px;
}

.title {
    font-size: 22px;
    font-family: system-ui;
    color: #363636;
}

.title2 {
    font-size: 14px !important;
    font-family: system-ui;
    color: #363636;
}

.title3 {
    font-size: 14px !important;
    font-family: system-ui;
    color: #363636;
    text-align: center;
    margin-left: 8px;
    margin-top: 20px;
}

.orangetitle {
    color: #ff7100;
    margin: 20px;
    font-size: 16px;
    font-weight: 500;
    float: right;
    position: relative;
    right: 160px;
}



::placeholder {
    font-size: 14px;
    font-family: system-ui;
}

.btnOrange {
    font-family: system-ui;
    float: right;
    font-size: 14px;
    background-color: #ff7100;
    margin: 0 5% 0 0;
    text-align: center;
    box-sizing: border-box;
    height: 50px;
    width: -moz-fit-content;
    width: 90%;
    border-radius: 25px;
    margin-left: 50px;
    font-weight: 500;
    color: white;
    border-color: none;
    border: none;

}

.btnOrange:hover {
    background-color: #d15f02;
}

.textandbars {
    display: flex;
    flex-direction: row;
    width: 90%;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 20px;
}

.horizontalbar {
    font-size: 13px;
    font-family: system-ui;
    color: #cccccc;
    position: relative;

    width: 25%;
    height: 0.5px;
    background-color: #cccccc;
}

.titlehorizontal {
    font-size: 13px;
    font-family: system-ui;
    color: #cccccc;
    position: relative;
    bottom: 10px;
    margin-right: 5px;
    margin-left: 5px;
}

.title2orange {
    font-size: 14px !important;
    font-family: system-ui;
    color: #ff7100;
    text-decoration: none;
    margin-right: 3px;
    cursor: pointer;
}

.apple {
    height: 17px;
    margin-left: 5px;

}

.google {
    margin-left: 5px;
}

.imgpadding {
    padding: 30px;
    margin-right: 20px;
}

.largetitle {
    font-size: 36px;
    color: #363636;
    font-family: system-ui;
}

