    monospace;
}

.RE-form-Number {
  height: 24px;
  width: 24px;
  line-height: 25px;
  position: absolute;
  margin-right: 1.3%;
  margin-top: 1px;
  border-radius: 50px;
  color: #0fca80;
  text-align: center;
  border: 2px solid #0fca80;
  font-family: Rubik;
  font-size: 18px;
  font-weight: 500;
}

.RE-form-text {
  padding-right: 50px;
  display: flex;
  justify-content: flex-end;
  font-size: 24px;
  font-family: system-ui;
  height: fit-content;
}

.RE-form-bg {
  background-color: #f5f5f5;
  padding-top: 70px;
}

.RE-form-text-number {
  display: flex;
  flex-direction: column;
  padding-top: 30px;
