  margin: auto;
  margin-bottom: 30px;
  margin-right: 140px;
  margin-left: 140px;
}

.rowright {
  right: 0px !important;
}

.REform-pic-text {
  text-align: center;
  position: relative;
  top: -20px;
  font-size: 16px;
  font-weight: 400;
  line-height: 27px;
  margin-top: 10px;
  font-family: system-ui;
  font-size: 12px;
  font-weight: 500;
  color: #646464;
}

.REform-pic {
  box-sizing: border-box;
  height: 112px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 1px 2px 4px 0 rgb(0 0 0 / 10%);
  cursor: pointer;
  width: 122px;
  margin: 0 auto;
  margin-bottom: 48px;
  position: relative;
  top: -8px;
  margin-left: 0px;
  margin-right: 50px;
}

.REform-pic:hover {
  border: 1px solid #ff7100;
  color: #ff7100;
}




.REform-pic-wrap {
  position: relative;
  top: 40px;
  display: flex;

}

