}

.form-hidden {
  height: 60px;
  overflow: hidden;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  margin-bottom: 20px;
  margin-left: 140px;
  margin-right: 140px;
}

.inner-hidden-form {
  background-color: #f9f9f9;
  position: relative;
  padding-left: 0;
  box-sizing: border-box;
  border-radius: 5px;
  box-shadow: 0 2px 4px 0 rgb(12 0 51 / 10%);
  text-align: right;
  height: 60px;
  margin: auto;
}

.form-hidden-text {
  font-size: 24px;
  font-family: system-ui;
